.header h1 {
  color: var(--main-color);
  text-align: center;
}

.main {
  width: 100%;
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

.form div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form label {
  margin: 5px;
  color: var(--main-color);
  font-weight: bold;
}

.name input,
.year select,
.gender select {
  font-family: "Open Sans", "Lato", sans-serif;
  font-size: 1.2rem;
  padding: 0.4rem;
  border-radius: 20px;
  border: solid 2px #ccc;
}

.submit button {
  padding: 1rem;
  font-size: 1.2rem;
  color: white;
  background-color: var(--main-color);
  cursor: pointer;
  border: none;
  border-radius: 20px;
  margin-top: 1.5rem;
}

.submit button:hover {
  background-color: #811e44;
}

.searchResult {
  max-width: 50rem;
  margin: 0 auto;
}
