@font-face {
  font-family: "AlloyInk";
  src: url("../../a-alloy-ink-font/AlloyInk-lgdWw.otf") format("opentype"),
    url("../../a-alloy-ink-font/AlloyInk-nRLyO.ttf") format("truetype");
}

.header {
  width: 100%;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--main-color);
  padding: 0 10%;
  margin-bottom: 4rem;
}

.logo {
  font-size: 2rem;
  color: white;
  font-family: "AlloyInk", "Lato", sans-serif;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.header li {
  margin-left: 3rem;
}

.header a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #fcb8d2;
}

.logo a {
  color: white;
}

.header a:hover,
.header a:active,
.header a.active {
  color: white;
}

.active {
  color: white;
}
